<template>
    <div class="container" style="height: 100vh">
        <div class="container-head">
            <el-popover placement="left" trigger="hover">
                <span>添加新会话</span>
                <span class="el-icon-plus addHistory" slot="reference" @click="addChart"/>
            </el-popover>
        </div>
        <div class="container-main" ref="mainRef">
            <div v-if="dataList?.length && dataList?.length > 0">
                <div v-for="(item, index) in dataList" :key="index">
                    <div class="main-title">
                        <QuestionComponent :question="item.question"/>
                    </div>
                    <div class="main-title">
                        <div style="display: flex;justify-content: space-between;padding-left: 8px;margin-bottom: 5px">
                            <div style="font-weight: bold; font-size: 14px">Answer：</div>
                            <div>
                <span style="margin-right: 8px;cursor: pointer;font-size: 16px"
                      class="el-icon-refresh-right"
                      v-if="item.refresh"
                      @click="onRefresh(item.question)"/>
                                <span class="el-icon-document-copy" style="cursor:pointer;font-size: 16px"
                                      v-if="item.success"
                                      v-clipboard:copy="item.answer"
                                      v-clipboard:success="onCopy"
                                      v-clipboard:error="onCopyError"
                                />
                            </div>
                        </div>
                        <div style="background-color:#303943;border-radius: 8px;padding:8px"
                             v-loading="index === dataList?.length - 1 && loading"
                             element-loading-background="rgb(255 255 255 / 28%)"
                             element-loading-spinner="el-icon-loading"
                        >
                            <div class="markdown-body" style="min-height: 24px;font-size: 14px">
                                <vue-markdown v-highlight :source="item.answer"/>
                            </div>
                        </div>
                    </div>
                    <el-divider v-if="index !== dataList?.length - 1"/>
                </div>
            </div>
            <div v-if="!dataList?.length || dataList?.length === 0"
                 style="margin:auto;width:80%;text-align: center;padding-top:100px ">
                <QuickSearch @quickSend="quickSend"/>
            </div>
        </div>
        <div class="container-foot">
            <div style="margin-bottom: 10px">
                <div style="display: flex;justify-content: center;height:20px">
                    <button @click="stop" class="stopStyle" v-if="stopVisible">stop generating</button>
                </div>
                <el-input placeholder="请输入内容" v-model="sendMessage" type="textarea" resize="none" id="textArea"
                          style="width:100%;display: flex;justify-content: center"
                          contenteditable="true"/>
            </div>
            <div style="display: flex;justify-content: end;color: #777777;width:97%;height:20px" id="sendContainer">
                <div>Enter 发送</div>
                <div class="sendIcon" @click="send" v-if="!loading">
                    <div style="rotate: 45deg;font-size: 16px;" class="el-icon-position"/>
                </div>
                <div class="loadingIcon" v-if="loading">
                    <div style="font-size: 16px;" class="el-icon-loading"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import QuestionComponent from "./questionComponent.vue";
import QuickSearch from "./quickSearch.vue";
import _ from 'lodash';
import highlight from "highlight.js";
import {fetchEventSource} from "@microsoft/fetch-event-source";
import {GPU, CPU} from './body'

export default {
    name: "MainPage",
    components: {"vue-markdown": VueMarkdown, QuestionComponent, QuickSearch},
    data() {
        return {
            loading: false,
            inputs: "",
            sendMessage: '',
            dataList: [],
            res: '',
            range: null,
            inputHeight: '',
            messageType: '',
            answer: '',
            delay: 50,
            stopStatus: false,
            stopVisible: false,
            ideaSelect: null,
            sendUrl: null,
            maxToken: null,
            controller: new AbortController(),
            modelType: 'CPU'
        };
    },
    methods: {
        addChart() {
            this.$confirm('确定要清空所有对话记录？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                this.dataList = []
            }).catch((e) => {
                console.debug(e)
            });
        },
        send() {
            if (_.isEmpty(_.trim(this.sendMessage))) {
                this.$message({
                    message: 'message is empty',
                    type: 'warning'
                })
                return;
            }
            this.loading = true
            this.dataList.push({question: this.ideaSelect ?? this.sendMessage, answer: ''})
            const inputs = this.sendMessage
            this.sendMessage = ''
            this.stopStatus = false
            this.$refs.mainRef.scrollTop = this.$refs.mainRef.scrollHeight
            const self = this
            self.stopVisible = true
            fetchEventSource(this.sendUrl ?? 'http://127.0.0.1:8080/completion', {
                method: 'POST',
                signal: self.controller.signal,
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(this.modelType === 'GPU' ? GPU(inputs, this.maxToken) : CPU(inputs, this.maxToken)),
                onerror() {
                    self.$message.error('返回错误!')
                    self.ideaSelect = null
                    self.loading = false
                    self.ideaSelect = null
                    self.stopVisible = false
                    self.controller.abort('onerror')
                    self.controller = new AbortController()
                    this.onclose()
                },
                onmessage({data}) {
                    const reg = /[|<]+end[>|]+|<\|endoftext\|>|## human/g
                    if (data) {
                        self.stopVisible = true
                        self.loading = false
                        const {content, stop, generated_text, token} = JSON.parse(data) ?? {}
                        const {text} = {...token}
                        if (stop) {
                            self.dataList = _.map(self.dataList, (a, i) => (_.size(self.dataList) - 1 === i ? {
                                ...a,
                                answer: _.replace(a.answer, reg, '')
                            } : a))
                            self.ideaSelect = null
                            self.stopVisible = false
                            self.controller.abort();
                            self.controller = new AbortController()
                            return;
                        }
                        if (content) {
                            self.dataList = _.map(self.dataList, (a, i) => (_.size(self.dataList) - 1 === i ? {
                                ...a, refresh: true, success: true, answer: a.answer + content
                            } : a))
                        } else if (text) {
                            self.dataList = _.map(self.dataList, (a, i) => (_.size(self.dataList) - 1 === i ? {
                                ...a, refresh: true, success: true, answer: a.answer + text
                            } : a))
                            if (generated_text) {
                                const answer = _.replace(generated_text, reg, '')
                                self.dataList = _.map(self.dataList, (a, i) => (_.size(self.dataList) - 1 === i ? {
                                    ...a, refresh: true, success: true, answer
                                } : a))
                                self.ideaSelect = null
                                self.stopVisible = false
                                self.controller.abort();
                                self.controller = new AbortController()
                            }
                        }
                        self.$refs.mainRef.scrollTop = self.$refs.mainRef.scrollHeight
                    }
                },
                onclose() {

                }
            })
        },
        async stop() {
            await this.controller.abort()
            this.controller = new AbortController()
            this.loading = false
            this.stopStatus = true
            this.stopVisible = false
        },
        quickSend(item) {
            this.sendMessage = item
            // 请求
        },
        onCopy() {
            this.$message.success('已复制到剪贴板')
        },
        onCopyError() {
            this.$message.warning('复制失败')
        },
        onRefresh(v) {
            this.sendMessage = v
            this.send()
        },
    },
    mounted() {
        window.addEventListener('message', (e) => {
            const {data} = e?.data
            if (_.isString(data)) {
                const z = JSON.parse(data) ?? {}
                const {range, sendText, sendUrl, maxToken, modelType} = {...z}
                this.modelType = modelType
                this.sendUrl = sendUrl
                console.debug('IDEA JSON', '------->', e,)
                console.debug('IDEA MESSAGE', z, '------->', range, sendText, sendUrl)
                const {inputs} = {...sendText}
                const {selectedText, end, start} = {...range}
                console.debug('range ---->', start, end, 'selectedText ----->', selectedText)
                this.ideaSelect = selectedText
                this.sendMessage = inputs
                this.maxToken = maxToken ? _.toNumber(maxToken) : 1024
                !modelType && this.$message.warning('请选择模型运行环境！')
                sendText && this.send()
            }
        })
        document.getElementById('textArea').addEventListener('keydown', e => {
            if (e.keyCode === 13 && !e.shiftKey) {
                e.preventDefault()
                this.send()
            }
        })
    },
    updated() {
        this.$el.querySelectorAll('pre').forEach(item => {
            highlight.highlightBlock(item)
        })
    },
};
</script>
<style scoped lang="less">
.addHistory {
  font-weight: bolder;
  border-radius: 4px;
  text-align: center;
  line-height: 26px;
  font-size: 18px;
  border: 1px solid #409eff;
  background-color: #409eff;
  color: white;
  cursor: pointer;
  margin: 0 20px;
  width: 26px;
  height: 26px
}

.stopStyle {
  padding-top: 2px;
  width: 130px;
  background-color: rgba(255, 255, 255, 0);
  color: white;
  outline: none;
  border: 1px solid #868484;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
}

.loadingIcon {
  width: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 16px;
  cursor: pointer;
  color: white;
}

.sendIcon {
  background-color: #409eff;
  border-radius: 3px;
  width: 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 16px;
  cursor: pointer;
  color: white;
}

::v-deep .markdown-body {
  background-color: #303943;
}

::v-deep .markdown-body p {
  padding: 0 12px;
  color: white;
}

::v-deep .el-icon-loading {
  color: white;
}

::v-deep .el-loading-mask {
  border-radius: 8px;
  justify-content: center;
  display: flex;
  align-items: center;
}

::v-deep .el-loading-spinner {
  border-radius: 8px;
  font-size: 22px;
  top: 26px;
}

.container {
  background-color: #3d3d3f;
  width: 100%;

  .container-head {
    height: 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 1px solid #817b7b;
    width: 98%;
    margin: 0 auto;
  }

  .main-box {
    cursor: pointer;
    display: flex;
    border-radius: 4px;
    margin: 14px 0 4px 20px;
    background-color: #409eff;
    width: 28px;
    color: #fff;
    justify-content: center;
    align-items: center;
  }

  .main-title {
    justify-content: space-between;
    padding: 6px 12px;
    color: #fff;
  }

  .container-main {
    width: 100%;
    overflow-y: scroll;
    height: calc(100vh - 234px);
  }

  .container-foot {
    width: 100%;
  }
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #817b7b;
}

::v-deep .el-textarea__inner {
  height: 120px;
  display: block;
  resize: vertical;
  padding: 10px 15px;
  box-sizing: border-box;
  width: 96%;
  color: black;
  background-color: white;
  background-image: none;
  border: none;
  border-radius: 4px;
  transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}

::v-deep .el-divider--horizontal {
  background-color: #6e6d6d;
  width: 98%;
  margin: 0 auto
}

::v-deep .markdown-body p {
  margin: 0
}

::v-deep .el-radio {
  color: white;
  margin-right: 16px;
}

</style>
