const GPU = (input, token) => ({
    "inputs": `##human:${input}|<end>|##assistant:`,
    "parameters": {
        "max_new_tokens": token ?? 1024,
        "temperature": 0.2,
        "top_p": 0.95,
        "do_sample": true,
        "repetition_penalty": 1,
        "stop": [
            "|<end>|", "<end>", "<|endoftext|>", "## human", "|end|", "|end>|", "<end|"
        ]
    }
})

const CPU = (input, token) => ({
    'frequency_penalty': 1.2,
    'n_predict': token ?? 1024,
    'prompt': `|<end>|## human:${input}|<end>|## assistant:`,
    'temperature': 0.2,
    'stream': true,
    'stop': ["|<end>|"]
})

export {GPU, CPU}