<template>
    <div>
        <div v-for="(item,index) in list" :key="index" style="color: white">
            <div @click="quickSend(item)"
                 style="border: 1px solid rgba(147,146,146,0.8);margin-bottom: 8px;border-radius: 16px;padding: 8px 0;cursor: pointer"
                 class="aaa">
                {{ item }}
            </div>
        </div>
    </div>
</template>


<script>
export default {
    name: "quickSearch",
    data() {
        return {
            list: [
                "Generate code in Java to convert a number from one base to another",
                "Generate code to implement a simple REST API in go",
                "How to set global variables in git",
                "Create an encrypted s3 bucket using the AWS CLI",
                "Explain B+ trees, give an example with code"
            ]
        }
    },
    methods: {
        quickSend(item) {
            this.$emit('quickSend', item)
        }
    }
}
</script>

<style scoped>
.aaa:hover {
    background-color: rgba(100, 98, 98, 0.58);
    transition: .3s;
}
</style>